@import url('https://fonts.googleapis.com/css?family=Lato');

* { 
  box-sizing: border-box;
}

body { 
  /* background-image: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%); */
  background: #41B3A3;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  min-height:  100vh;
  font-family: 'Lato', sans-serif;
  margin: 0;
}

h1 {
  text-align: center;
}

.timeline-container {
  display: flex;
  flex-direction: column;
  margin:  40px 0;
  position: relative;
}

.timeline-container:after {
   background: #e17b77;
   content: '';
   position: absolute;
   left: calc(50% - 2px);
   width: 4px;
   height: 100%;
}

.timeline-item {
  display: flex;
  justify-content: flex-end;
  padding-right: 30px;
  margin: 10px 0 ;
  width: 50%;
}

.timeline-item:nth-child(odd) {
  align-self: flex-end;
  justify-content: flex-start;
  padding-right: 0;
  padding-left: 30px;
}

.timeline-item-content {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background: #E8A87C;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding: 15px;
  position: relative;
  width: 800px;
  max-width: 70%;
  text-align: right;

}
.timeline-item-content:after {
  content: "";
  background: #E8A87C;
  width: 15px;
  height: 15px;
   position: absolute;
  right: -7.5px; 
    top: calc(50% - 7.5px);
    transform: rotate(45deg);
  box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
}


.timeline-item:nth-child(odd) .timeline-item-content {
text-align: left;
align-items: flex-start;
}

.timeline-item:nth-child(odd) .timeline-item-content:after {
right: auto;
left: -7.5px;
box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}

.timeline-item-content .tag {
  color: white;
  font-size: 12px;
  font-weight: bold;
  padding: 5px;
  position: absolute;
  top: 5px;
  left: 5px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.timeline-item:nth-child(odd) .timeline-item-content .tag {
  left: auto;
  right: 5px;
}

.timeline-item-content .time {
  color: #777;
  font-size: 12px;

}

.timeline-item-content .time p {
  font-size: 16px;
  line-height: 24px;
  margin: 15px 0;
  max-width: 250px;
}

.timeline-item-content a {
  color:#333;
  text-decoration: none;
  font-size: 14px;
}

.timeline-item-content a::after {
  content: ' ►';
  font-size: 12px;
}

.timeline-item-content .circle {
background-color: #fff;
border-radius: 50%;
border: 3px solid #e17b77;
position: absolute;
top: calc(50% - 10px);
right: -40px;
z-index: 100;
width: 20px;
height: 20px;
}

.timeline-item:nth-child(odd) .timeline-item-content .circle {
  right: auto;
  left: -40px;
}


@media only screen and (max-width: 767px){
.timeline-item-content, 
.timeline-item:nth-child(odd) .timeline-item-content {
  padding: 15px 10px;
  text-align: center;
  align-items: center;
}

.timeline-item-content .tag {
  width: calc(100% - 10px);
}
.timeline-item-content time {
  margin-top: 20px;
}

.timeline-item-content a {
  text-decoration: underline;
}

.timeline-item-content a::after {
  display: none;
}
}